import React, { useState, useEffect } from "react";
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import ThumbUpIcon from '@material-ui/icons/ThumbUp';
import Title from './Title';
import AuthService from "../../services/auth.service";
import UserService from "../../services/user.service";
import Modal from 'react-bootstrap/Modal'
//import Button from '@material-ui/core/Button';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';


/*function preventDefault(event) {
  event.preventDefault();
}*/

const useStyles = makeStyles((theme) => ({
  seeMore: {
    marginTop: theme.spacing(3),
  },
  button: {
    marginTop: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },

}));

const ApproveUsers = () => {
  const classes = useStyles();

  const [users, setUsers] = useState(undefined);
  const [currentUser, setCurrentUser] = useState(null);
  //  const [currentIndex, setCurrentIndex] = useState(-1);
  const [approveShow, setApproveShow] = useState(false);
  const [deleteShow, setDeleteShow] = useState(false);
  const [message, setMessage] = useState("");
  const [successful, setSuccessful] = useState(false);

  const handleApproveShow = (index) => {
    setCurrentUser(users[index]);
    setSuccessful(false);
    setMessage("");
    setApproveShow(true);
  }
  const handleApproveClose = () => {
    setApproveShow(false);
    retrieveUsers();
  }
  const handleApproveConfirm = () => {
    UserService.setKeeperRole(currentUser._id)
      .then(res => {
        setMessage(res.data.message);
        setSuccessful(true);
      })
      .catch(err => {
        console.log(err);
        console.log(err.message);
        //setMessage(err.data.message);
        setSuccessful(false);
      });
  }

  const handleDeleteShow = (index) => {
    setCurrentUser(users[index]);
    setSuccessful(false);
    setMessage("");
    setDeleteShow(true);
  }
  const handleDeleteConfirm = () => {
    UserService.deleteUser(currentUser._id)
      .then(res => {
        setMessage(res.data.message);
        setSuccessful(true);
      })
      .catch(err => {
        console.log(err);
//        setMessage(err.data.message);
        setSuccessful(false);
      });
    setDeleteShow(false);
    setTimeout(function () {
      retrieveUsers();
    }, 100);
  }
  const handleDeleteDeny = () => {
    setDeleteShow(false);
  }

  useEffect(() => {
    retrieveUsers();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  async function retrieveUsers() {
    const user = AuthService.getCurrentUser();
    if (user) {
      await UserService.getAllUsers()
        .then(res => {
          setUsers(res.data);
        })
        .catch(err => {
          console.log(err);
        });
    }
  };

  return (
    <>
      <Grid item xs={12} md={4} lg={3}>
        <Paper className={classes.paper}>
          <React.Fragment>
            <Title>Approve registered users</Title>
            <Table size="small">
              <TableHead>
                <TableRow>
                  <TableCell>Username</TableCell>
                  <TableCell align="right">Actions</TableCell>
                </TableRow>
              </TableHead>
              {users && (
                <TableBody>
                  {users.map((user, index) => (
                    <TableRow key={index}>
                      <TableCell>{user.username}</TableCell>
                      <TableCell align="right">
                        <Link color="primary" onClick={() => handleApproveShow(index)}>
                          <ThumbUpIcon />
                        </Link>
                        <Link color="primary" onClick={() => handleDeleteShow(index)}>
                          <DeleteForeverIcon />
                        </Link>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              )}
            </Table>
          </React.Fragment>
        </Paper>
      </Grid>

      {currentUser && (
        <Modal show={approveShow} onHide={handleApproveClose} centered>
          <Modal.Header closeButton>
            <Modal.Title>Approve user {currentUser.username}?</Modal.Title>
          </Modal.Header>
          {message && (
            <Modal.Body>
              <Box mt={1}>
                <div className={successful ? "alert alert-success" : "alert alert-danger"} role="alert">
                  {message}
                </div>
              </Box>
            </Modal.Body>
          )}
          <Modal.Footer>
            <div className={classes.button}>
              <Grid container spacing={2} justify="center">
                <Grid item>
                  <Button variant="outlined" color="primary" onClick={handleApproveClose}>
                    Close
                  </Button>
                </Grid>
                <Grid item>
                  <Button variant="contained" color="primary" onClick={handleApproveConfirm} >
                    Approve
                  </Button>
                </Grid>
              </Grid>
            </div>
          </Modal.Footer>
        </Modal>
      )}
      {currentUser && (
        <Modal show={deleteShow} onHide={handleDeleteDeny} centered>
          <Modal.Header closeButton>
            <Modal.Title>Delete {currentUser.username}?</Modal.Title>
          </Modal.Header>
          {message && (
            <Modal.Body>
              <Box mt={1}>
                <div className={successful ? "alert alert-success" : "alert alert-danger"} role="alert">
                  {message}
                </div>
              </Box>
            </Modal.Body>
          )}
          <Modal.Footer>
            <div className={classes.button}>
              <Grid container spacing={2} justify="center">
                <Grid item>
                  <Button variant="outlined" color="primary" onClick={handleDeleteDeny}>
                    Close
                  </Button>
                </Grid>
                <Grid item>
                  <Button variant="contained" color="primary" onClick={handleDeleteConfirm} >
                    Delete
                  </Button>
                </Grid>
              </Grid>
            </div>
          </Modal.Footer>
        </Modal>
      )}
    </>
  );
}

export default ApproveUsers;