import React, { useState, useEffect } from "react";
import { Switch, Route } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

import AddTutorial from "./components/AddTutorial";
import Tutorial from "./components/Tutorial";
import TutorialsList from "./components/TutorialList";

import AuthService from "./services/auth.service";
import AuthenticatedRoute from "./AuthenticatedRoute";

import DashBoard from "./components/dashboard/Dashboard";
import SignInSide from "./components/SignInSide";
import SignUp from "./components/SignUp";
import HomePage from "./components/Home";

const App = () => {
  const [showKeeperBoard, setShowKeeperBoard] = useState(false);
  const [showAdminBoard, setShowAdminBoard] = useState(false);
  const [currentUser, setCurrentUser] = useState(undefined);

  useEffect(() => {
    const user = AuthService.getCurrentUser();

    if (user) {
      setCurrentUser(user);
      setShowKeeperBoard(user.roles.includes("ROLE_KEEPER"));
      setShowAdminBoard(user.roles.includes("ROLE_ADMIN"));
    }
  }, []);

  const logOut = () => {
    AuthService.logout();
  };

  return (
    <div>
      <Switch>
        <Route path="/login" component={SignInSide} />
        <Route exact path="/register" component={SignUp} />
        <Route exact path={["/", "/h"]} component={HomePage} />
        
        <AuthenticatedRoute
          path="/db"
          component={DashBoard}
          appProps={{ currentUser, showAdminBoard, showKeeperBoard }}
        />
        {(showAdminBoard || showKeeperBoard) && (
          <Route path="/db" component={DashBoard} />
        )}
        {/*<Route path="/db" component={DashBoard} />*/}

        <Route exact path="/tutorials" component={TutorialsList} />
        <Route exact path="/add" component={AddTutorial} />
        <Route path="/tutorials/:id" component={Tutorial} />
      </Switch>
    </div>
  );
};

export default App;
