import React from "react";
import { Route, Redirect } from "react-router-dom";
import AuthService from "./services/auth.service";

export default function AuthenticatedRoute({ component: C, appProps, ...rest }) {
  return (
    <Route
      {...rest}
      render={props =>
        ( (AuthService.getCurrentUser() ? (AuthService.getCurrentUser().roles.includes("ROLE_KEEPER") || AuthService.getCurrentUser().roles.includes("ROLE_ADMIN")) : false) )
          ? <C {...props} {...appProps} />
          : <Redirect to="/" />
      }
    />
  );
}

