import React, { useState, useEffect } from "react";
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import EditIcon from '@material-ui/icons/Edit';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import Title from './Title';
import NoteService from '../../services/note.service';
import AuthService from "../../services/auth.service";
import Modal from 'react-bootstrap/Modal'
//import Button from '@material-ui/core/Button';
import EditNote from './EditNote';
import NewNote from './NewNote';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';


/*function preventDefault(event) {
  event.preventDefault();
}*/

const useStyles = makeStyles((theme) => ({
  seeMore: {
    marginTop: theme.spacing(3),
  },
  button: {
    marginTop: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },

}));

const Notes = () => {
  const classes = useStyles();

  const [notes, setNotes] = useState(undefined);
  const [currentNote, setCurrentNote] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(-1);
  //  const [currentIndex, setCurrentIndex] = useState(-1);
  const [editShow, setEditShow] = useState(false);
  const [newShow, setNewShow] = useState(false);
  const [deleteShow, setDeleteShow] = useState(false);

  const handleEditShow = (index) => {
    setCurrentNote(notes[index]);
    setEditShow(true);
  }
  const handleEditClose = () => {
    setEditShow(false);
    retrieveNotes();
  }

  const handleNewShow = () => {
    setNewShow(true);
  }
  const handleNewClose = () => {
    setNewShow(false);
    retrieveNotes();
  }

  const handleDeleteShow = (index) => {
    setCurrentNote(notes[index]);
    setCurrentIndex(index);
    setDeleteShow(true);
  }
  const handleDeleteConfirm = () => {
    NoteService.delete(currentNote._id)
      .then(() => {
        let user = JSON.parse(localStorage.getItem('user'));
        user.notes.splice(currentIndex, 1);
        localStorage.setItem("user", JSON.stringify(user));
      })
      .catch(e => {
        console.log(e);
      });
    setDeleteShow(false);
    setTimeout(function () {
      retrieveNotes();
    }, 100);
  }
  const handleDeleteDeny = () => {
    setDeleteShow(false);
  }

  useEffect(() => {
    retrieveNotes();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const retrieveNotes = () => {
    const user = AuthService.getCurrentUser();
    if (user && user.notes) {
      setNotes(user.notes);
    }
  };

  return (
    <>
      <Grid item xs={12}>
        <Paper className={classes.paper}>
          <React.Fragment>
            <Title>Notes</Title>
            <Table size="small">
              <TableHead>
                <TableRow>
                  <TableCell>Note</TableCell>
                  <TableCell align="center">Date</TableCell>
                  <TableCell align="right">Actions</TableCell>
                </TableRow>
              </TableHead>
              {notes && (
                <TableBody>
                  {notes.map((note, index) => (
                    <TableRow key={index}>
                      <TableCell>{note.content}</TableCell>
                      <TableCell align="center">{new Date(note.createdAt).toLocaleDateString()}   {new Date(note.createdAt).toLocaleTimeString()}</TableCell>
                      <TableCell align="right">
                        <Link color="primary" onClick={() => handleEditShow(index)}>
                          <EditIcon />
                        </Link>
                        <Link color="primary" onClick={() => handleDeleteShow(index)}>
                          <DeleteForeverIcon />
                        </Link>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              )}
            </Table>
            <div className={classes.seeMore}>
              <Link color="primary" onClick={handleNewShow}>
                Create new...
              </Link>
            </div>
          </React.Fragment>
        </Paper>
      </Grid>

      {currentNote && (
        <Modal show={editShow} onHide={handleEditClose} centered>
          <Modal.Header closeButton>
            <Modal.Title>Update note</Modal.Title>
          </Modal.Header>
          <Modal.Body><EditNote noteid={currentNote._id} /></Modal.Body>
        </Modal>
      )}
      <Modal show={newShow} onHide={handleNewClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Create note</Modal.Title>
        </Modal.Header>
        <Modal.Body><NewNote /></Modal.Body>
      </Modal>
      {currentNote && (
        <Modal show={deleteShow} onHide={handleDeleteDeny} centered>
          <Modal.Header closeButton>
            <Modal.Title>Delete?</Modal.Title>
          </Modal.Header>
          <Modal.Footer>
            <div className={classes.button}>
              <Grid container spacing={2} justify="center">
                <Grid item>
                  <Button variant="outlined" color="primary" onClick={handleDeleteDeny}>
                    Close
                  </Button>
                </Grid>
                <Grid item>
                  <Button variant="contained" color="primary" onClick={handleDeleteConfirm} >
                    Delete
                  </Button>
                </Grid>
              </Grid>
            </div>
          </Modal.Footer>
        </Modal>
      )}
    </>
  );
}

export default Notes;