import React, { useState, useEffect } from 'react';
import { useTheme } from '@material-ui/core/styles';
import { LineChart, Line, XAxis, YAxis, Label, ResponsiveContainer, Tooltip } from 'recharts';
import Title from './Title';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Link from '@material-ui/core/Link';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Modal from 'react-bootstrap/Modal'
import Typography from '@material-ui/core/Typography';
import HiveService from '../../services/hive.service';
import AuthService from "../../services/auth.service";
import DataService from "../../services/data.service";
import NewHive from './NewHive';
import EditHive from './EditHive';
import Button from '@material-ui/core/Button';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import PostAddIcon from '@material-ui/icons/PostAdd';
import EditIcon from '@material-ui/icons/Edit';

const useStyles = makeStyles((theme) => ({
  depositContext: {
    flex: 1,
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 340,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  allignRight: {
    display: 'flex',
    justifyContent: 'right',
  },
}));

export default function Hives() {
  const theme = useTheme();
  const classes = useStyles();
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

  const [hives, setHives] = useState(undefined);
  const [currentHive, setCurrentHive] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(-1);
  const [hiveData, setHiveData] = useState(undefined);
  const [measure, setMeasure] = useState("temperature"); //temperature,humidity,pressure,weight

  const [editShow, setEditShow] = useState(false);
  const [newShow, setNewShow] = useState(false);
  const [deleteShow, setDeleteShow] = useState(false);

  const handleEditShow = () => {
    setEditShow(true);
  }
  const handleEditClose = () => {
    setEditShow(false);
    retrieveHives();
  }

  const handleNewShow = () => {
    setNewShow(true);
  }
  const handleNewClose = () => {
    setNewShow(false);
    retrieveHives();
  }

  const handleChangeMeasure = (event) => {
    setMeasure(event.target.value);
  }
  const handleChangeHive = (event) => {
    setCurrentIndex(event.target.value);
    setCurrentHive(hives[event.target.value]);
    retrieveHiveData();
  }

  const handleDeleteShow = () => {
    setDeleteShow(true);
  }
  const handleDeleteConfirm = () => {
    HiveService.delete(currentHive._id)
      .then(() => { })
      .catch(e => {
        console.log(e);
      });
    setDeleteShow(false);
    setTimeout(function () {
      retrieveHives();
    }, 100);
  }
  const handleDeleteDeny = () => {
    setDeleteShow(false);
  }

  useEffect(() => {
    retrieveHives();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentHive]);

  const retrieveHives = () => {
    const user = AuthService.getCurrentUser();
    if (user) {
      HiveService.getAllOfKeeper(user.id)
        .then(response => {
          setHives(response.data);
          if (response.data.length > 0) {
            retrieveHiveData();
          }
        })
        .catch(e => {
          console.log(e);
        });
      //setNotes(user.notes);
    }
  };
  const retrieveHiveData = () => {
    if (currentHive) {
      DataService.getAllOfHive(currentHive._id)
        .then(response => {
          var a = response.data;
          a.forEach((dat) => {
            dat.createdAt = new Date(dat.createdAt).toLocaleTimeString();
          });
          setHiveData(a);
        })
        .catch(e => {
          console.log(e);
        });
      //setNotes(user.notes);
    }
  };

  return (
    <>
      <Grid item xs={12} md={8} lg={9}>
        <Paper className={fixedHeightPaper}>
          <React.Fragment>
            <Title>{currentHive ? currentHive.title + " " + measure : "Choose a beehive"}</Title>
            {(hiveData && hiveData.length > 0) && (
              <ResponsiveContainer>
                <LineChart
                  data={hiveData ? hiveData.slice(hiveData.length - 10, hiveData.length) : null}
                  margin={{
                    top: 16,
                    right: 16,
                    bottom: 0,
                    left: 24,
                  }}
                >
                  <XAxis dataKey="createdAt" stroke={theme.palette.text.secondary} interval="preserveStartEnd" />
                  <YAxis stroke={theme.palette.text.secondary}>
                    <Label
                      angle={270}
                      position="left"
                      style={{ textAnchor: 'middle', fill: theme.palette.text.primary }}
                    >
                      {measure}
                    </Label>
                  </YAxis>
                  <Tooltip />
                  <Line type="monotone" dataKey={measure} stroke={theme.palette.primary.main} dot={true} />
                </LineChart>
              </ResponsiveContainer>
            )}
          </React.Fragment>
        </Paper>
      </Grid>
      <Grid item xs={12} md={4} lg={3}>
        <Paper className={fixedHeightPaper}>
          <React.Fragment>
            <Title>Current hive data</Title>
            {(hiveData && hiveData.length > 0) && (
              <>
                <Typography color="textSecondary" className={classes.depositContext}>
                  Temperature: {hiveData[hiveData.length - 1].temperature}*C <br />
                  Humidity: {hiveData[hiveData.length - 1].humidity}% <br />
                  Pressure: {hiveData[hiveData.length - 1].pressure}kPa <br />
                  Weight: {hiveData[hiveData.length - 1].weight}kg
                </Typography>
              </>
            )}
            {!(hiveData && hiveData.length > 0) && (
              <>
                <Typography color="textSecondary" className={classes.depositContext}>
                  Temperature: --*C <br />
                  Humidity: --% <br />
                  Pressure: --kPa <br />
                  Weight: --kg
                </Typography>
              </>
            )}
            <FormControl className={classes.formControl}>
              <InputLabel id="demo-simple-select-label">Selected Hive</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={currentIndex === -1 ? "" : currentIndex}
                onChange={handleChangeHive}
              >
                <MenuItem value="" disabled>
                  <em>None</em>
                </MenuItem>
                {hives && hives.map((hive, index) => (
                  <MenuItem value={index} key={index}>{hive.title}</MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl className={classes.formControl}>
              <InputLabel id="demo-simple-select-label">Measure to graph</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={measure}
                onChange={handleChangeMeasure}
              >
                <MenuItem value={"temperature"}>Temperature</MenuItem>
                <MenuItem value={"humidity"}>Humidity</MenuItem>
                <MenuItem value={"pressure"}>Pressure</MenuItem>
                <MenuItem value={"weight"}>Weight</MenuItem>
              </Select>
            </FormControl>
            <div style={{ display: "flex", alignItems: 'center', justifyContent: 'flex-end' }}>
              <Link color="primary" onClick={handleNewShow}>
                <PostAddIcon />
              </Link>
              {currentHive && (
                <>
                  <Link color="primary" style={{ marginLeft: "auto", alignItems: 'center' }} onClick={handleEditShow}>
                    <EditIcon />
                  </Link>
                  <Link color="primary" style={{ marginLeft: "auto", alignItems: 'center' }} onClick={handleDeleteShow}>
                    <DeleteForeverIcon />
                  </Link>
                </>
              )}
            </div>
          </React.Fragment>
        </Paper>
      </Grid>
      {currentHive && (
        <Modal show={editShow} onHide={handleEditClose} centered>
          <Modal.Header closeButton>
            <Modal.Title>Update hive</Modal.Title>
          </Modal.Header>
          <Modal.Body><EditHive hiveid={currentHive._id} /></Modal.Body>
        </Modal>
      )}
      <Modal show={newShow} onHide={handleNewClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Add a new hive</Modal.Title>
        </Modal.Header>
        <Modal.Body><NewHive /></Modal.Body>
      </Modal>
      {currentHive && (
        <Modal show={deleteShow} onHide={handleDeleteDeny} centered>
          <Modal.Header closeButton>
            <Modal.Title>Delete?</Modal.Title>
          </Modal.Header>
          <Modal.Footer>
            <div className={classes.button}>
              <Grid container spacing={2} justify="center">
                <Grid item>
                  <Button variant="outlined" color="primary" onClick={handleDeleteDeny}>
                    Close
                  </Button>
                </Grid>
                <Grid item>
                  <Button variant="contained" color="primary" onClick={handleDeleteConfirm} >
                    Delete
                  </Button>
                </Grid>
              </Grid>
            </div>
          </Modal.Footer>
        </Modal>
      )}
    </>
  );
}
