import React, { useState, useEffect } from "react";
import HiveService from '../../services/hive.service';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';


const useStyles = makeStyles((theme) => ({
  button: {
    marginTop: theme.spacing(4),
  },
  depositContext: {
    flex: 1,
  },
}));

const EditHive = props => {
  const classes = useStyles();

  const initialHiveState = {
    _id: null,
    title: ""
  };
  const [currentHive, setCurrentHive] = useState(initialHiveState);
  const [message, setMessage] = useState("");
  const [successful, setSuccessful] = useState(false);

  const getHive = id => {
    HiveService.get(id)
      .then(response => {
        setCurrentHive(response.data);
        //console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  };

  useEffect(() => {
    if (props.hiveid)
      getHive(props.hiveid);
  }, [props.hiveid]);

  const handleInputChange = event => {
    const { name, value } = event.target;
    setCurrentHive({ ...currentHive, [name]: value });
  };

  const updateHive = () => {
    HiveService.update(currentHive._id, currentHive)
      .then(response => {
        console.log(response.data);
        setMessage("Hive was updated successfully!");
        setSuccessful(true);
      })
      .catch(e => {
        setMessage("Failed to update hive!");
        setSuccessful(false);
        console.log(e);
      });
  };

  return (
    <div>
      {currentHive ? (
        <div className="edit-form">
          {message && (
            <Box mt={1}>
              <div className={successful ? "alert alert-success" : "alert alert-danger"} role="alert">
                {message}
              </div>
            </Box>
          )}
          <form>
            <div className="form-group">
              <Typography component="h1" variant="h5">
                Title
              </Typography>
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="title"
                label="Hives title"
                name="title"
                autoFocus
                value={currentHive.title}
                onChange={handleInputChange}
              />
              <Typography color="textSecondary" className={classes.depositContext}>
                Hive ID: {currentHive._id}
              </Typography>
            </div>
          </form>

          <div className={classes.button}>
            <Grid container spacing={2} justify="center">
              <Grid item>
                <Button type="submit" variant="contained" color="primary" onClick={updateHive} >
                  Update
                </Button>
              </Grid>
            </Grid>
          </div>
        </div>
      ) : (
          <div>
            <br />
            <p>Please click on a Hive...</p>
          </div>
        )}
    </div>
  );
};

export default EditHive;