import React, { useState } from "react";
import NoteService from '../../services/note.service';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import AuthService from "../../services/auth.service";


const useStyles = makeStyles((theme) => ({
  button: {
    marginTop: theme.spacing(4),
  },
}));

const NewNote = () => {
  const classes = useStyles();

  const initialNoteState = {
    content: "",
    user: AuthService.getCurrentUser().id
  };
  const [currentNote, setCurrentNote] = useState(initialNoteState);
  const [message, setMessage] = useState("");
  const [successful, setSuccessful] = useState(false);

  const handleInputChange = event => {
    const { name, value } = event.target;
    setCurrentNote({ ...currentNote, [name]: value });
  };

  const createNote = () => {
    NoteService.create(currentNote)
      .then(response => {
        console.log(response.data);
        setMessage("The note was created successfully!");
        setSuccessful(true);

        let user = JSON.parse(localStorage.getItem('user'));
        user.notes.splice(user.notes.length, 0, response.data);
        localStorage.setItem("user", JSON.stringify(user));
      })
      .catch(e => {
        setMessage("Failed to create note!");
        setSuccessful(false);
        console.log(e);
      });
  };

  return (
    <div>
      {currentNote ? (
        <div className="edit-form">
          {message && (
            <Box mt={1}>
              <div className={successful ? "alert alert-success" : "alert alert-danger"} role="alert">
                {message}
              </div>
            </Box>
          )}
          <form>
            <div className="form-group">
              <TextareaAutosize
                type="text"
                className="form-control"
                id="content"
                name="content"
                value={currentNote.content}
                onChange={handleInputChange}
              />
            </div>
          </form>

          <div className={classes.button}>
            <Grid container spacing={2} justify="center">
              <Grid item>
                <Button type="submit" variant="contained" color="primary" onClick={createNote} >
                  Create
                  </Button>
              </Grid>
            </Grid>
          </div>
        </div>
      ) : (
          <div>
            <br />
            <p>Please click on a Note...</p>
          </div>
        )}
    </div>
  );
};

export default NewNote;