import React, { useState, useEffect } from "react";
import NoteService from '../../services/note.service';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';


const useStyles = makeStyles((theme) => ({
  button: {
    marginTop: theme.spacing(4),
  },
}));

const EditNote = props => {
  const classes = useStyles();

  const initialNoteState = {
    _id: null,
    content: ""
  };
  const [currentNote, setCurrentNote] = useState(initialNoteState);
  const [message, setMessage] = useState("");
  const [successful, setSuccessful] = useState(false);

  const getNote = id => {
    NoteService.get(id)
      .then(response => {
        setCurrentNote(response.data);
        //console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  };

  useEffect(() => {
    if (props.noteid)
      getNote(props.noteid);
  }, [props.noteid]);

  const handleInputChange = event => {
    const { name, value } = event.target;
    setCurrentNote({ ...currentNote, [name]: value });
  };

  const updateNote = () => {
    NoteService.update(currentNote._id, currentNote)
      .then(response => {
        console.log(response.data);
        setMessage("The note was updated successfully!");
        setSuccessful(true);

        let user = JSON.parse(localStorage.getItem('user'));
        for (var i = 0; i < user.notes.length; i++) {
          if (user.notes[i]._id === currentNote._id) {
            user.notes[i].content = currentNote.content;
            break;
          }
        }
        localStorage.setItem("user", JSON.stringify(user));
      })
      .catch(e => {
        setMessage("Failed to update note!");
        setSuccessful(false);
        console.log(e);
      });
  };

  return (
    <div>
      {currentNote ? (
        <div className="edit-form">
          {message && (
            <Box mt={1}>
              <div className={successful ? "alert alert-success" : "alert alert-danger"} role="alert">
                {message}
              </div>
            </Box>
          )}
          <form>
            <div className="form-group">
              <TextareaAutosize
                type="text"
                className="form-control"
                id="content"
                name="content"
                value={currentNote.content}
                onChange={handleInputChange}
              />
            </div>
          </form>

          <div className={classes.button}>
            <Grid container spacing={2} justify="center">
              <Grid item>
                <Button type="submit" variant="contained" color="primary" onClick={updateNote} >
                  Update
                  </Button>
              </Grid>
            </Grid>
          </div>
        </div>
      ) : (
          <div>
            <br />
            <p>Please click on a Note...</p>
          </div>
        )}
    </div>
  );
};

export default EditNote;