import axios from 'axios';
import authHeader from './auth-header';

const API_URL = '/api';

class NoteService {
  getAll() {
    return axios.get(API_URL + "/notes", { headers: authHeader() });
  }

  get(id) {
    return axios.get(API_URL + `/notes/${id}`, { headers: authHeader() });
  }

  create(data) {
    return axios.post(API_URL + "/notes", data, { headers: authHeader() });
  }

  update(id, data) {
    return axios.put(API_URL + `/notes/${id}`, data, { headers: authHeader() });
  }

  delete(id) {
    return axios.delete(API_URL + `/notes/${id}`, { headers: authHeader() });
  }
}

export default new NoteService();