import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Title from './Title';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import AuthService from "../../services/auth.service";


const useStyles = makeStyles((theme) => ({
  depositContext: {
    flex: 1,
  },
  seeMore: {
    marginTop: theme.spacing(3),
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
}));


export default function Orders() {
  const classes = useStyles();

  const [userData, setUserData] = useState(undefined);

  useEffect(() => {
    retrieveUser();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const retrieveUser = () => {
    setUserData(AuthService.getCurrentUser());
  };

  return (
    <Grid item xs={12} md={4} lg={3}>
      <Paper className={classes.paper}>
        <React.Fragment>
          {userData && (
            <>
              <Title>{userData.username} profile</Title>
              <Typography component="p" variant="h5">
                ID:
              </Typography>
              <ul>
                <Typography color="textSecondary" className={classes.depositContext} align="center">
                  <li>{userData.id}</li>
                </Typography>
              </ul>
              <Typography component="p" variant="h5">
                E-Mail:
              </Typography>
              <ul>
                <Typography color="textSecondary" className={classes.depositContext} align="center">
                  <li>{userData.email}</li>
                </Typography>
              </ul>
              <Typography component="p" variant="h5">
                Authorities:
              </Typography>
              <ul>
                <Typography color="textSecondary" className={classes.depositContext} align="center">
                  {userData.roles &&
                    userData.roles.map((role, index) => <li key={index}>{role}</li>)}
                </Typography>
              </ul>
            </>
          )}
        </React.Fragment>
      </Paper>
    </Grid>
  );
}
