import React, { useState, useEffect } from 'react';
//import { Route } from "react-router-dom";
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import Box from '@material-ui/core/Box';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import NotificationsIcon from '@material-ui/icons/Notifications';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import { mainListItems, secondaryListItems } from './listItems';
import Modal from 'react-bootstrap/Modal'
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import Button from '@material-ui/core/Button';
import Hidden from '@material-ui/core/Hidden';
import Notes from './Notes';
import Hives from './Hives';
import Profile from './Profile';
import ApproveUsers from './ApproveUsers';
import AuthenticatedRoute from '../../AuthenticatedRoute';
import AuthService from "../../services/auth.service";
import NotificationService from "../../services/notification.service";
import HiveService from '../../services/hive.service';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Justinas Munius IFF-7/5.  '}
      {new Date().getFullYear()}
      {'.'}

    </Typography>
  );
}

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  // necessary for content to be below app bar
  //toolbar: theme.mixins.toolbar,
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  bg: {
    backgroundImage: 'url(http://source.unsplash.com/collection/8626361)',
    backgroundRepeat: 'no-repeat',
    backgroundColor:
      theme.palette.type === 'light' ? theme.palette.grey[50] : theme.palette.grey[900],
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    opacity: 0.3,

    position: 'absolute',
    left: '50%',
    top: '50%',
    transform: 'translate(-50%, -50%)',
    //height: `calc(100% - ${footerHeight}px)`,
    height: '100%',
    width: '100%',
    zIndex: -1,
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    height: '100vh',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  drawerPaperMob: {
    position: 'relative',
    whiteSpace: 'nowrap',
    height: '100vh',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
}));

export default function Dashboard(props) {
  const { window } = props;
  const classes = useStyles();
  const theme = useTheme();

  const [notifs, setNotifs] = useState(undefined);
  const [notifShow, setNotifShow] = useState(false);

  const [open, setOpen] = useState(false);
  const [openMobile, setOpenMobile] = useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };
  const handleDrawerMobileToggle = () => {
    setOpenMobile(!openMobile);
  };

  const handleNotifClose = () => {
    setNotifShow(false);
    retrieveNotifs();
  }
  const handleNotifOpen = () => {
    setNotifShow(true);
    retrieveNotifs();
  }
  const handleNotifDelete = (index) => {
    NotificationService.delete(notifs[index]._id);
    retrieveNotifs();
  }
  const handleNotifClearAll = () => {
    notifs.forEach(not => {
      NotificationService.delete(not._id);
    });
    setNotifShow(false);
    retrieveNotifs();
  }

  useEffect(() => {
    retrieveNotifs();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  async function retrieveNotifs() {
    const user = AuthService.getCurrentUser();
    if (user) {
      var hives = [];
      await HiveService.getAllOfKeeper(user.id)
        .then(response => {
          hives = response.data;
          //setNotifs(response.data);
        })
        .catch(e => {
          console.log(e);
        });
      var notifications = [];
      await Promise.all(hives.map(async (hive) => {
        await NotificationService.getAllOfHive(hive._id)
          .then(response => {
            for (const a of response.data) {
              notifications.splice(notifications.length, 0, a);
            }
          })
          .catch(e => {
            console.log(e);
          });
      }));
      setNotifs(notifications);
      hives = null;
      notifications = null;
    }
  };

  //const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      <CssBaseline />
      <div className={classes.bg}></div>
      {/* TOP */}
      <AppBar position="absolute" className={clsx(classes.appBar, open && classes.appBarShift)}>
        <Toolbar className={classes.toolbar}>
        <Hidden xsDown implementation="css">
            <IconButton
              edge="start"
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              className={clsx(classes.menuButton, open && classes.menuButtonHidden)}
            >
              <MenuIcon />
            </IconButton>
          </Hidden>
          <Hidden smUp implementation="css">
            <IconButton
              edge="start"
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerMobileToggle}
              className={clsx(classes.menuButton, open && classes.menuButtonHidden)}
            >
              <MenuIcon />
            </IconButton>
          </Hidden>
          <Typography component="h1" variant="h6" color="inherit" noWrap className={classes.title}>
            Dashboard
          </Typography>
          <IconButton color="inherit" onClick={handleNotifOpen}>
            <Badge badgeContent={notifs ? notifs.length : ""} color="secondary">
              <NotificationsIcon />
            </Badge>
          </IconButton>
        </Toolbar>
      </AppBar>
      {/* SIDE */}
      <Hidden xsDown implementation="css">
        <Drawer
          variant="permanent"
          classes={{
            paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
          }}
          open={open}
        >
          <div className={classes.toolbarIcon}>
            <IconButton onClick={handleDrawerClose}>
              <ChevronLeftIcon />
            </IconButton>
          </div>
          <Divider />
          {/* <List>{mainListItems}</List> */}
          <List>{mainListItems}</List>
          {AuthService.getCurrentUser().roles.includes("ROLE_ADMIN") && (
            <>
              <Divider />
              <List>{secondaryListItems}</List>
            </>
          )}
        </Drawer>
      </Hidden>
      <Hidden smUp implementation="css">
        <Drawer
          container={container}
          variant="temporary"
          anchor={theme.direction === 'rtl' ? 'right' : 'left'}
          classes={{
            paper: classes.drawerPaper,
          }}
          open={openMobile}
          onClose={handleDrawerMobileToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}

        >
          <div className={classes.toolbarIcon}>
            <IconButton onClick={handleDrawerMobileToggle}>
              <ChevronLeftIcon />
            </IconButton>
          </div>
          <Divider />
          {/* <List>{mainListItems}</List> */}
          <List>{mainListItems}</List>
          {AuthService.getCurrentUser().roles.includes("ROLE_ADMIN") && (
            <>
              <Divider />
              <List>{secondaryListItems}</List>
            </>
          )}
        </Drawer>
      </Hidden>      
      {/* MAIN */}
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          <Grid container spacing={3} justify="center">
            <AuthenticatedRoute path="/db/notes" component={Notes} />
            <AuthenticatedRoute path="/db/hives" component={Hives} />
            <AuthenticatedRoute path="/db/profile" component={Profile} />
            <AuthenticatedRoute path="/db/approve" component={ApproveUsers} />
          </Grid>
          <Box pt={4}>
            <Copyright />
          </Box>
        </Container>
      </main>
      {/* MODAL */}
      <Modal show={notifShow} onHide={handleNotifClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Notifications</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Table size="small">
            {notifs && (
              <TableBody>
                {notifs.map((notif, index) => (
                  <TableRow key={index}>
                    <TableCell>{notif.level}</TableCell>
                    <TableCell>{notif.content}</TableCell>
                    <TableCell align="center">{new Date(notif.createdAt).toLocaleDateString()}   {new Date(notif.createdAt).toLocaleTimeString()}</TableCell>
                    <TableCell align="right">
                      <Link color="primary" onClick={() => handleNotifDelete(index)}>
                        <DeleteForeverIcon />
                      </Link>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            )}
          </Table>
        </Modal.Body>
        <Modal.Footer>
          <div className={classes.button}>
            <Grid container spacing={2} justify="center">
              <Grid item>
                <Button variant="outlined" color="primary" onClick={handleNotifClose}>
                  Close
                </Button>
              </Grid>
              <Grid item>
                <Button variant="contained" color="primary" onClick={handleNotifClearAll}>
                  Clear all
                </Button>
              </Grid>
            </Grid>
          </div>
        </Modal.Footer>
      </Modal>

    </div>
  );
}
