import React, { useState } from "react";
import HiveService from '../../services/hive.service';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import AuthService from "../../services/auth.service";
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles((theme) => ({
  button: {
    marginTop: theme.spacing(4),
  },
}));

const NewHive = () => {
  const classes = useStyles();

  const initialHiveState = {
    title: "",
    keeper: AuthService.getCurrentUser().id
  };
  const [currentHive, setCurrentHive] = useState(initialHiveState);
  const [message, setMessage] = useState("");
  const [successful, setSuccessful] = useState(false);

  const handleInputChange = event => {
    const { name, value } = event.target;
    setCurrentHive({ ...currentHive, [name]: value });
  };

  const createHive = () => {
    HiveService.create(currentHive)
      .then(response => {
        console.log(response.data);
        setMessage("Hive was created successfully! ID=" + response.data._id);
        setSuccessful(true);
      })
      .catch(e => {
        setMessage("Failed to create hive!");
        setSuccessful(false);
        console.log(e);
      });
  };

  return (
    <div>
      {currentHive ? (
        <div className="edit-form">
          {message && (
            <Box mt={1}>
              <div className={successful ? "alert alert-success" : "alert alert-danger"} role="alert">
                {message}
              </div>
            </Box>
          )}
          <form>
            <div className="form-group">
              <Typography component="h1" variant="h5">
                Title
              </Typography>
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="title"
                label="Hives title"
                name="title"
                autoFocus
                onChange={handleInputChange}
              />
            </div>
          </form>

          <div className={classes.button}>
            <Grid container spacing={2} justify="center">
              <Grid item>
                <Button type="submit" variant="contained" color="primary" onClick={createHive} >
                  Create
                  </Button>
              </Grid>
            </Grid>
          </div>
        </div>
      ) : (
          <div>
            <br />
            <p>Please click on a Hive...</p>
          </div>
        )}
    </div>
  );
};

export default NewHive;