import React, { useState, useEffect } from "react";
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import BlurLinearIcon from '@material-ui/icons/BlurLinear';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Paper from '@material-ui/core/Paper';
//import Link from '@material-ui/core/Link';
//import { Home } from '@material-ui/icons';
import AuthService from "../services/auth.service";
import useReactFontLoader from 'react-font-loader';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Justinas Munius IFF-7/5.  '}
      {new Date().getFullYear()}
      {'.'}

    </Typography>
  );
}

const footerHeight = 70;

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    display: 'flex',
    flexDirection: 'column',
    minHeight: '100vh',
  },
  icon: {
    marginRight: theme.spacing(2),
  },
  bg: {
    //background: 'rgba(255, 255, 255, 0.3)',
    //backgroundColor: theme.palette.background.paper,
    backgroundImage: 'url(http://source.unsplash.com/collection/8626361)',
    backgroundRepeat: 'no-repeat',
    backgroundColor:
      theme.palette.type === 'light' ? theme.palette.grey[50] : theme.palette.grey[900],
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    opacity: 0.3,

    position: 'absolute',
    left: '50%',
    top: '50%',
    transform: 'translate(-50%, -50%)',
    height: `calc(100% - ${footerHeight}px)`,
    width: '100%',
  },
  heroContent: {
    fontFamily: 'Langar',
    background: 'rgba(255, 255, 255, 0.8)',
    //backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(8, 0, 6),
    position: 'absolute',
    left: '50%',
    top: '50%',
    transform: 'translate(-50%, -50%)',
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {
    paddingTop: '56.25%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  footer: {
    height: footerHeight,
    padding: theme.spacing(3, 2),
    position: 'relative',
    marginTop: 'auto',
    backgroundColor:
      theme.palette.type === 'light' ? theme.palette.grey[200] : theme.palette.grey[800],
  },
}));

const HomePage = (props) => {
  const classes = useStyles();

  const [showKeeperBoard, setShowKeeperBoard] = useState(false);
  const [showAdminBoard, setShowAdminBoard] = useState(false);
  const [currentUser, setCurrentUser] = useState(undefined);

  useReactFontLoader({
    url: 'https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap',
  })

  useEffect(() => {
    const user = AuthService.getCurrentUser();

    if (user) {
      setCurrentUser(user);
      setShowKeeperBoard(user.roles.includes("ROLE_KEEPER"));
      setShowAdminBoard(user.roles.includes("ROLE_ADMIN"));
    }
  }, []);

  const logOut = () => {
    AuthService.logout();
    window.location.reload();
  };

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="relative">
        <Toolbar>
          <BlurLinearIcon className={classes.icon} />
          <Typography variant="h6" color="inherit" noWrap>
            Hive Manager
          </Typography>
        </Toolbar>
      </AppBar>
      <main>
        {/* Hero unit */}
        <div className={classes.bg}></div>
        <Paper className={classes.heroContent}>
          <Container maxWidth="md">
            <Typography style={{ fontFamily: 'Montserrat' }} component="h1" variant="h2" align="center" color="textPrimary" gutterBottom>
              Welcome to HiveManager
            </Typography>
            <Typography variant="h5" align="center" color="textSecondary" paragraph>
              Tool to make you manage your beehives with ease.
            </Typography>
            <hr />
            <div className={classes.heroButtons}>
              {!currentUser && (
                <Grid container spacing={2} justify="center">
                  <Grid item>
                    <Button variant="contained" color="primary" onClick={() => props.history.push("/login")}>
                      Sign In
                </Button>
                  </Grid>
                  <Grid item>
                    <Button variant="outlined" color="primary" onClick={() => props.history.push("/register")}>
                      Sign Up
                </Button>
                  </Grid>
                </Grid>
              )}
              {(currentUser && (showKeeperBoard || showAdminBoard)) && (
                <Grid container spacing={2} justify="center">
                  <Grid item>
                    <Button variant="contained" color="primary" onClick={() => props.history.push("/db/hives")}>
                      DashBoard
                </Button>
                  </Grid>
                  <Grid item>
                    <Button variant="outlined" color="primary" onClick={logOut}>
                      Log Out
                </Button>
                  </Grid>
                </Grid>
              )}
              {(currentUser && !(showKeeperBoard || showAdminBoard)) && (
                <Grid container spacing={2} justify="center">
                  <Grid item>
                    <Typography variant="h5" align="center" color="textSecondary" paragraph>
                      You will be able to use website when administrator approves you as a keeper.
                    </Typography>
                  </Grid>
                  <Grid item>
                    <Button variant="outlined" color="primary" onClick={logOut}>
                      Log Out
                    </Button>
                  </Grid>
                </Grid>
              )}
            </div>
          </Container>
        </Paper>
      </main>
      {/* Footer */}
      <footer className={classes.footer}>
        <Container maxWidth="sm">
          <Copyright />
        </Container>
      </footer>
      {/* End footer */}
    </div>
  );
}

export default HomePage;